.navbarContainer{
    position: relative;
    top: 0;
    text-align: center;
    width: 100%;
    justify-content: center;
    align-items: center;
    margin: 1rem 0% 0% 0%;
    grid-area: Navbar;

}
.navigation_list{
    padding: 0%;
    text-align: center;
    display: inline;
}
.navigation_list li {
    margin-right: 1rem;
    display: inline;
}
.navigation_list img{
    display: inline;
    width: auto;
    height: 100%;
    max-height: 30px;
    max-width: 30px;
}